.card{
    border: 2px solid grey;
    width: 220px;
    padding: 10px;
    border-radius: 12px;
    margin: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card .img{
    width: 220px;
    height: 120px;
    border-radius: 12px;
}